import React from 'react'
import logo from './new.png'
import bgmi from './bgmi.webp'
import freefire from './freefire.png'
import newstate from './newsta.png'
import hand from './gif.gif'
import style from './main.css'
import QuickStats from './QuickStats'
import Features from './Features'
import ContactUs from './ContactUs'


export default function Main() {

    
   
   
    return (
       <>
       <div className="header" style={style}>
       

       
       <div className="heading" >
       <h1 className="main-heading" style={style}>Pubg new state</h1>

       <h3 className="minor-head" style={style}>ULtimate Esports Platform<img className="handClass" src={hand} alt="" style={style} /></h3>

       <p style={{color:"#787A91",fontFamily: "'Montserrat', sans-serif"}}>Are you addicted to Online Games? Have you ever thought of earning through Online Gaming or from Playing Multiplayer Mobile Games? What if you were rewarded for playing your favorite games and that too of which you are addicted to? Well, this is the Platform which makes this possible.</p>

       <button type="button" className="btn btn-outline-success my-2" style={{textTransform:"uppercase"}}><i className="fas fa-download"></i> Download Now</button>

      
       </div>

        <div className="header-image" style={style}>
        <img className="logoclass" src={logo} alt="" style={style} />
        </div>
       </div>

       <div className="heading1 text-center" style={{backgroundColor:"#23283a"}}>
       <div className="randomDiv" style={style}>

       <h1 style={{color:"#fff",fontFamily: "'Montserrat', sans-serif",paddingTop:"1rem"}}>Available Games</h1>
       
       </div>

       <div className="available" style={style}>

      <div className="card card-main" style={style}>
      <img src={bgmi} className="card-img-top" alt="..."/>
      <div className="card-body">
      <h5 className="card-title" style={{fontFamily: "'Montserrat', sans-serif"}}>BGMI</h5>
      </div>
      </div>

      <div className="card card-main" style={style}  >
      <img src={freefire} className="card-img-top" alt="..."/>
      <div className="card-body">
      <h5 className="card-title" style={{fontFamily: "'Montserrat', sans-serif"}}>Free Fire</h5>
      </div>
      </div>
      <div className="card card-main" style={style}   >
      <img src={newstate} className="card-img-top" alt="..."/>
      <div className="card-body">
      <h5 className="card-title" style={{fontFamily: "'Montserrat', sans-serif"}}>New State</h5>
      </div>
      </div>
      <div className="card card-main" style={style}  >
      <img src={newstate} className="card-img-top" alt="..."/>
      <div className="card-body">
      <h5 className="card-title" style={{fontFamily: "'Montserrat', sans-serif"}}>New State</h5>
      </div>
      </div>
      
      </div>

       </div>
       <QuickStats/>
       <Features/>
       <ContactUs/>
      
      

      
        

    
   

       </>
    )
}
