import React from "react";
import feature from "./features.css";
import player from './pla.png'
import chicken from './chicken.png'
import friends from './friends.png'
import wallet from './wallet.png'

export default function Features() {
  return (
    <>
      <div className="features" style={feature}>
        <h1 className="features-heading" style={feature}>
          Some Cool Features
        </h1>
     
      <div className="features-card" style={feature}>
        <div className="card  featuresCard" style={feature}>
          
          <img src={player} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title features-title" style={feature}>Join Custom Tournaments</h5>
          </div>
        </div>
        <div className="card  featuresCard" style={feature}>
          <img src={chicken} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title features-title" style={feature}>Get Prizes on Each Kills</h5>
          </div>
        </div>
        <div className="card  featuresCard" style={feature}>
          <img src={friends} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title features-title" style={feature}>Play with your friends</h5>
          </div>
        </div>
        <div className="card  featuresCard" style={feature}>
          <img src={wallet} className="card-img-top" alt="..." />
          <div className="card-body">
            <h5 className="card-title features-title" style={feature}>Get Rewards Directly in Paytm</h5>
          </div>
        </div>
      </div>
      </div>
    </>
  );
}
