import React from 'react'
import feature from './features.css'
import download from './download.png'
export default function ContactUs() {
    return (
        <>
        <div className="head text-center" style={feature}>
            <h1 className="contact-header"b style={feature}>Are you ready? Give it a try...</h1>
            <a href="/"><img className="image-contact" src={download} style={feature} alt="" /></a>
        </div>
        </>
    )
}
