import React from "react";
import scrim from "./scrims.webp";
import room from "./room.png";
import content from "./content.jpg";
import style from './main.css'
import style1 from "./features.css"
export default function QuickStats() {
  return (
    <>
      <div className="head">
        <h1
          style={{
            textTransform: "capitalize",
            color: "#23283a",
            fontFamily: "'Montserrat', sans-serif",
            textAlign: "center",
          }}
        >
          Here's What we offer
        </h1>
      </div>
      <div className="offer" style={style1}>

        <div class="card card-offer shadow-lg p-3 mb-5 bg-white rounded" style={style}>
          <img class="card-img-top" src={scrim} alt="..."/>
          <div class="card-body">
            <h5 class="card-title text-center" style={{fontFamily: "'Montserrat', sans-serif",fontWeight:"700",color:"#012970"}}>Scrims</h5>
            <p class="card-text text-center" style={{fontFamily: "'Montserrat', sans-serif",color:"#444444"}} >
            We host scrims for teams interested in competing against the best and proving themselves.
            </p>
            
          </div>
        </div>
        <div class="card card-offer shadow-lg p-3 mb-5 bg-white rounded" style={style}>
          <img class="card-img-top" src={room}  alt="..."/>
          <div class="card-body">
            <h5 class="card-title text-center"  style={{fontFamily: "'Montserrat', sans-serif",color:"#012970",fontWeight:"700"}}>Custom Rooms</h5>
            <p class="card-text text-center"  style={{fontFamily: "'Montserrat', sans-serif",color:"#444444;"}}>
            We host daily paid custom rooms for those who want to practise while earning money.
            </p>
            
          </div>
        </div>
        <div class="card card-offer shadow-lg p-3 mb-5 bg-white rounded" style={style}>
          <img class="card-img-top" src={content} alt="..."  />
          <div class="card-body">
            <h5 class="card-title text-center"  style={{fontFamily: "'Montserrat', sans-serif",color:"#012970",fontWeight:"700"}}>Content Creation</h5>
            <p class="card-text text-center"  style={{fontFamily: "'Montserrat', sans-serif",color:"#444444;"}}>
            We'll curate innovative content like memes, short videos etc which will engage your audience and skyrocket your brand value
            </p>
            
          </div>
        </div>
        <div class="card card-offer shadow-lg p-3 mb-5 bg-white rounded" style={style}>
          <img class="card-img-top" src={content} alt="..."  />
          <div class="card-body">
            <h5 class="card-title text-center"  style={{fontFamily: "'Montserrat', sans-serif",color:"#012970",fontWeight:"700"}}>Content Creation</h5>
            <p class="card-text text-center"  style={{fontFamily: "'Montserrat', sans-serif",color:"#444444;"}}>
            We'll curate innovative content like memes, short videos etc which will engage your audience and skyrocket your brand value
            </p>
            
          </div>
        </div>
      </div>
    </>
  );
}
